body {
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', Microsoft JhengHei, sans-serif;
  background-color: #151515;
}
.game-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: auto;
}
.board {
  padding: 15px;
  width: 320px;
  background-color: #222;
}

.row {
  display: flex;
}
.dot {
  display: inline-block;
  position: relative;
  width: 5px;
  height: 5px;
  border-radius: 10px;
}

.dot div {
  position: absolute;
  background-color: #2d3034;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  top: -2px;
  left: -2px;
  z-index: 10;
}
.line {
  display: inline-block;
  position: relative;
  width: 5px;
  height: 30px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.line.B {
  background-color: #1565C0;
}

.line.R {
  background-color: #F50057;
}

.line .trigger {
  position: absolute;
  width: 10px;
  height: 30px;
  right: -4px;
}

.line.even {
  display: inline-block;
  width: 30px;
  height: 5px;
}

.line.even .trigger {
  position: absolute;
  width: 30px;
  height: 10px;
  left: 0;
  top: -4px;
}

.line.N.current-B:hover {
  background-color: #1565C0;
  cursor: pointer;
}

.line.N.current-R:hover {
  background-color: #F50057;
  cursor: pointer;
}
.box {
  display: inline-block;
  width: 30px;
  height: 30px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.box.B {
  background-color: #0D47A1;
}

.box.R {
  background-color: #C51162;
}
.score-board {
  width: 330px;
  background-color: #2d3034;
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
}

.score-board .score {
  font-size: 25px;
  min-width: 50px;
}

.score-board .score.B {
  color: #1565C0;
}

.score-board .score.R {
  color: #F50057;
  text-align: end;
}

.reset {
  background-color: unset;
  padding-top: 2px;
  width: 30px;
  height: 30px;
  border-radius: 30px;
  color: #e6e6e6;
  cursor: pointer;
  border: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.reset:hover {
  background-color: rgba(0, 0, 0, 0.301)
}

.reset:focus {
  outline: none;
}
.current-color {
  height: 3px;
  width: 350px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  flex-shrink: 0;
}

.current-color.B {
  background-color: #1565C0;
}

.current-color.R {
  background-color: #F50057;
}
.win-box {
  background-color: #3f3f3f;
  color: #e6e6e6;
  padding: 0 10px;
  border-radius: 3px;
  margin-bottom: 10px;
  height: 0;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.win-box.show {
  padding: 5px 10px;
  height: 20px;
}
.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.header {
  background-color: #242424;
  height: 50px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  flex-shrink: 0;
}

.logo {
  height: 30px;
  padding: 10px;
  background-color: #2d3034;
  color: #e6e6e6;
  float: left;
}

.title {
  float: left;
  line-height: 50px;
  color: #e6e6e6;
  font-size: 20px;
  padding-left: 15px;
  font-weight: bold;
}

.ghLogo {
  float: right;
  height: 30px;
  padding: 10px;
  background-color: #2d3034;
  color: #e6e6e6;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.ghLogo:hover {
  background-color: rgb(53, 53, 53);
}
.footer {
  padding: 10px 10px;
  padding-bottom: 0;
  font-size: 16px;
  height: 30px;
  position: relative;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-color: #222;
  flex-shrink: 0;
  color: #e6e6e6;
}
